import React, { useState } from 'react';
import { motion } from 'framer-motion';
import styled from 'styled-components';

const Modal = styled(motion.div)`
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  background: rgba(0,0,0,0.85);
  backdrop-filter: blur(12px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
`;

const ModalContent = styled.div`
  background: #0a0a0a;
  padding: 2.5rem;
  border-radius: 24px;
  max-width: 420px;
  text-align: center;
  border: 1px solid rgba(255,255,255,0.1);
  box-shadow: 0 20px 40px rgba(0,0,0,0.3);
`;

const WaitlistModal = ({ onClose }) => {
  const [email, setEmail] = useState('');

  return (
    <Modal
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
      onClick={onClose}
    >
      <ModalContent onClick={e => e.stopPropagation()}>
        <h2 style={{ fontSize: '2rem', marginBottom: '0.5rem' }}>Get Early Access</h2>
        <p style={{ opacity: 0.8, marginBottom: '1.5rem' }}>
          Join 10,000+ dreamers. Launch in 2026.
        </p>
        <input
          type="email"
          placeholder="your@email.com"
          value={email}
          onChange={e => setEmail(e.target.value)}
          style={{
            width: '100%',
            padding: '1rem',
            marginBottom: '1rem',
            borderRadius: '12px',
            border: '1px solid #333',
            background: '#111',
            color: 'white',
            fontSize: '1rem'
          }}
        />
        <button
          onClick={() => {
            alert(`, ${email || ''}!   .`);
            onClose();
          }}
          style={{
            background: '#1d4ed8',
            color: 'white',
            padding: '1rem',
            borderRadius: '50px',
            width: '100%',
            fontWeight: '600',
            marginBottom: '1rem'
          }}
        >
          Join Waitlist
        </button>
        <button onClick={onClose} style={{ color: '#888', fontSize: '0.9rem' }}>
          Maybe later
        </button>
      </ModalContent>
    </Modal>
  );
};

export default WaitlistModal;