import React, { useState } from 'react';
import { motion } from 'framer-motion';
import styled from 'styled-components';
import DownloadButton from './DownloadButton';
import WaitlistModal from './WaitlistModal';

const HeroSection = styled.section`
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
  overflow: hidden;
`;

const VideoBG = styled.video`
  position: absolute;
  top: 0; left: 0;
  width: 100%; height: 100%;
  object-fit: cover;
  opacity: 0.3;
  z-index: 0;
`;

const Hero = () => {
  const [showModal, setShowModal] = useState(false);

  return (
    <HeroSection>
      <VideoBG autoPlay muted loop>
        <source src="/nebula.mp4" type="video/mp4" />
      </VideoBG>

      <motion.div
        initial={{ opacity: 0, y: 50 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 1 }}
        style={{ zIndex: 2, maxWidth: '800px', padding: '0 2rem' }}
      >
        <h1 style={{ fontSize: 'clamp(3rem, 8vw, 6rem)', margin: 0, letterSpacing: '-0.03em' }}>
          VisionDo.ai
        </h1>
        <p style={{ fontSize: 'clamp(1.3rem, 3vw, 2rem)', margin: '1rem 0 2rem', opacity: 0.9 }}>
          You dream. It plans.
        </p>
        <p style={{ opacity: 0.8, marginBottom: '2rem' }}>
          AI turns your goals into daily tasks. From lost to unstoppable.
        </p>
        <DownloadButton onClick={() => setShowModal(true)}>
          Get Early Access
        </DownloadButton>
      </motion.div>

      {showModal && <WaitlistModal onClose={() => setShowModal(false)} />}
    </HeroSection>
  );
};

export default Hero;