import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

// Styled Header: Fixed top bar like x.ai/Appleminimal nav
const Nav = styled.nav`
  position: fixed;
  top: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.8); // Semi-transparent dark
  backdrop-filter: blur(10px); // Apple-style blur
  z-index: 1000;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const Logo = styled(Link)`
  font-size: 1.5rem;
  font-weight: bold;
  font-family: ${({ theme }) => theme.fonts.heading};
  color: ${({ theme }) => theme.colors.primary};
  letter-spacing: -0.02em; // Apple tight kerning
`;

const NavLinks = styled.div`
  display: flex;
  gap: 2rem;
  a {
    font-size: 1rem;
    color: ${({ theme }) => theme.colors.text};
    transition: color 0.3s ease;
    &:hover {
      color: ${({ theme }) => theme.colors.accent};
    }
  }
  @media (max-width: 768px) {
    display: none; // Mobile: Hide nav, use hamburger if needed (simplified)
  }
`;

// Header Component: Navigation bar
const Header = () => {
  return (
    <Nav>
      <Logo to="/">visiondo.ai</Logo>
      <NavLinks>
        <Link to="/">Home</Link>
        <Link to="/about">About</Link>
        <Link to="/features">Features</Link>
        <Link to="/team">Team</Link>
        <Link to="/team">EN</Link>
      </NavLinks>
    </Nav>
  );
};

export default Header;