import React from 'react';
import { motion } from 'framer-motion';
import styled from 'styled-components';

const Section = styled.section`
  padding: 8rem 2rem;
  background: #0f0f0f;
  overflow: hidden;
  position: relative;
`;

const Container = styled.div`
  max-width: 1300px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 5rem;
  align-items: center;

  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    text-align: center;
    gap: 3rem;
  }
`;

// ===  ===
const TextContent = styled(motion.div)`
  color: white;
  padding-left: 1.5rem;
  max-width: 540px;

  @media (max-width: 768px) {
    padding-left: 0;
    margin: 0 auto;
  }
`;

const Title = styled.h2`
  font-size: 3.6rem;
  font-weight: 900;
  background: linear-gradient(135deg, #00f5ff, #0072ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 1.5rem;
  line-height: 1.08;
  letter-spacing: -1.5px;

  @media (max-width: 768px) {
    font-size: 2.9rem;
  }
`;

const Tagline = styled.p`
  font-size: 1.4rem;
  color: #e8e8e8;
  line-height: 1.7;
  margin-bottom: 2.2rem;
  font-weight: 500;
`;

const Benefits = styled.ul`
  list-style: none;
  padding: 0;
  margin: 0;
`;

const Benefit = styled(motion.li)`
  font-size: 1.2rem;
  color: #f5f5f5;
  margin-bottom: 1.1rem;
  display: flex;
  align-items: flex-start;
  gap: 1rem;

  @media (max-width: 768px) {
    justify-content: center;
    text-align: left;
  }

  svg {
    width: 24px;
    height: 24px;
    stroke: #00f5ff;
    flex-shrink: 0;
    margin-top: 0.1rem;
  }
`;

// ===  ===
const DevicesWrapper = styled(motion.div)`
  display: flex;
  justify-content: flex-end;
  padding-right: 2rem;
  position: relative;
  perspective: 1500px;

  @media (max-width: 768px) {
    justify-content: center;
    padding-right: 0;
  }
`;

//     ,  3D-
const Tablet = styled(motion.div)`
  position: absolute;
  bottom: -20px;
  left: -70px;
  width: 400px;
  height: 540px;
  background: #111;
  border-radius: 48px;
  padding: 16px;
  box-shadow: 
    0 12px 35px rgba(0, 0, 0, 0.25),
    0 0 18px rgba(0, 245, 255, 0.07);
  border: 12px solid #000;
  z-index: 1;
  opacity: 0.94;

  /* 3D-:    */
  transform: rotateY(-8deg) rotateX(2deg);

  &::before {
    content: '';
    position: absolute;
    top: 28px;
    left: 50%;
    transform: translateX(-50%);
    width: 80px;
    height: 8px;
    background: #222;
    border-radius: 4px;
  }

  &::after {
    content: '';
    position: absolute;
    bottom: 28px;
    left: 50%;
    transform: translateX(-50%);
    width: 50px;
    height: 50px;
    background: #000;
    border-radius: 50%;
    border: 6px solid #222;
  }

  @media (max-width: 768px) {
    display: none; /*    */
  }
`;

const TabletScreen = styled.div`
  width: 100%;
  height: 100%;
  background-image: url('${props => props.image}');
  background-size: cover;
  background-position: center;
  border-radius: 32px;
  box-shadow: inset 0 0 25px rgba(0, 0, 0, 0.5);
`;

//    
const Phone = styled(motion.div)`
  position: relative;
  z-index: 2;
  width: 340px;
  height: 680px;
  background: #000;
  border-radius: 48px;
  padding: 16px;
  box-shadow: 
    0 18px 45px rgba(0, 0, 0, 0.3),
    0 0 28px rgba(0, 245, 255, 0.12);
  border: 11px solid #111;

  @media (max-width: 768px) {
    width: 300px;
    height: 600px;
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.35);
  }

  &::before {
    content: '';
    position: absolute;
    top: 24px;
    left: 50%;
    transform: translateX(-50%);
    width: 70px;
    height: 8px;
    background: #222;
    border-radius: 4px;
    z-index: 2;
  }
`;

const PhoneScreen = styled.div`
  width: 100%;
  height: 100%;
  background-image: url('${props => props.image}');
  background-size: cover;
  background-position: center;
  border-radius: 32px;
  position: relative;
  overflow: hidden;

  &::after {
    content: '';
    position: absolute;
    inset: 0;
    background: linear-gradient(135deg, transparent 40%, rgba(0, 245, 255, 0.06) 100%);
    border-radius: 32px;
    pointer-events: none;
  }
`;

// 
const TABLET_SCREENSHOT = "/images/tablet-preview.png";
const PHONE_SCREENSHOT = "/images/phone-preview.png";

const AboutApp = () => {
  return (
    <Section>
      <Container>
        {/* ===  === */}
        <TextContent
          initial={{ opacity: 0, x: -80 }}
          whileInView={{ opacity: 1, x: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.9, ease: "easeOut" }}
        >
          <Title>AI Tracking Manager</Title>
          <Tagline>
            One goal. Full execution. Zero excuses.
          </Tagline>

          <Benefits>
            {[
              "50+ micro-tasks from a single goal",
              "AI researches tools, courses, links",
              "3 daily tasks  click to done",
              "Missed a day? Plan auto-updates",
              "Real-time progress. Always."
            ].map((text, i) => (
              <Benefit
                key={i}
                initial={{ opacity: 0, x: -40 }}
                whileInView={{ opacity: 1, x: 0 }}
                viewport={{ once: true }}
                transition={{ delay: i * 0.12, duration: 0.6 }}
              >
                <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="3">
                  <path d="M5 13l4 4L19 7"/>
                </svg>
                {text}
              </Benefit>
            ))}
          </Benefits>
        </TextContent>

        {/* ===  === */}
        <DevicesWrapper
          initial={{ opacity: 0, scale: 0.85 }}
          whileInView={{ opacity: 1, scale: 1 }}
          viewport={{ once: true }}
          transition={{ duration: 1, ease: "easeOut" }}
        >
          {/*     , 3D- */}
          <Tablet
            initial={{ opacity: 0, x: -60, y: 30 }}
            whileInView={{ opacity: 0.94, x: 0, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 1.1, delay: 0.1 }}
          >
            <TabletScreen image={TABLET_SCREENSHOT} />
          </Tablet>

          {/*      */}
          <Phone
            initial={{ opacity: 0, scale: 0.9, y: 20 }}
            whileInView={{ opacity: 1, scale: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 1, delay: 0.3 }}
          >
            <PhoneScreen image={PHONE_SCREENSHOT} />
          </Phone>
        </DevicesWrapper>
      </Container>
    </Section>
  );
};

export default AboutApp;